import React from 'react';
import {Box, Typography, Avatar, Grid} from '@material-ui/core';
import Typed from "react-typed";
import { makeStyles } from '@material-ui/core/styles';
import avatar from './curly2.jpg';

const useStyles = makeStyles(theme=>({
avatar:
{
	
	width: theme.spacing(15),
	height: theme.spacing(15),
    margin: theme.spacing(1)
} ,
title:{
	color: "tomato"

},
subtitle :
{ 
	color: 'tan',
	marginBottom: "3rem"
}
,
typedwrapper :
{
	position: "absolute",
	top: "50%",
	left: "50%",
    transform:"translate(-50%,-50%)",
	width:"100vw",
    textAlign:"center",
	zIndex:"1"
	
}
}));




const Header = () => {
	const classes = useStyles()
	return (
		<Box className={classes.typedwrapper}>
			<Grid container justify="center">
             <Avatar className={classes.avatar} src={avatar} alt="Jermin Mansour"/>
			</Grid>

			<Typography  className={classes.title}  variant="h4">
			 <Typed
                strings={[
                    'JERMIN MANSOUR']}
           
                    typeSpeed={40}
     
            
                />

			</Typography>

				<Typography className={classes.subtitle}  variant="h5">
			 <Typed
                strings={['FRONT-END DEVELOPER','HTML','CSS','JAVASCRIPT','REACT JS','REACT HOOKS','GIT','GITHUB','NODE.JS','NPM','JSON',"WEB API'S", ]}
           
                    typeSpeed={40}
                    backSpeed={60}
     
             loop >
            </Typed>

			</Typography>
		</Box>
	);
}

export default Header