import React from 'react';
import {Typography,Box} from "@material-ui/core";
import {makeStyles} from '@material-ui/core/styles';
import NavBar from './NavBar';

const useStyles = makeStyles (theme=>({
	container: 
	{
		background: '#233',

	}
	,

	timeline:
	{
		position: "relative",
		padding:"1rem",
		margin:"0 auto",
	
		"&:before":
		{
			content: "''",
			color:"red",
			position: "absolute",
			height:"100%",
			border:"1px solid tan",
			right:'40px',
			top:0

		},
		"&:after":
		{
			content:"''",
			color:"green",
			display: "table",
			clear:"both"
		},
		[theme.breakpoints.up("md")]:{
			padding:"2rem",
			"&:before":
			{
				left:"calc(50% - 1px)",
				right:"auto"
			}
		},
	
		
	},
	timelineitem:
	{
		padding:"1rem",
		borderBottom:"1px solid tan",
		position:"relative",
		margin:"1rem 3rem 1rem 1rem",
		clear: "both", 
	   

		"&:after":
        { 
        	position:"absolute",
        	content:"''",
		},
		"&:before":
		{
			position:'absolute',
			content:"''",
			top:"calc(50%-5px)",
			right:"-0.6rem",
			borderStyle:"solid",
			borderColor:"tomato tomato transparent transparent",
			borderWidth:"0.6rem",
			transform:"rotate(45deg)", 
		},  
		[theme.breakpoints.up("md")]:
		{
			width:"44%",
			margin:"1rem",
			"&:nth-of-type(2n)":
			{
				float:"right",
				margin:"1rem",
				borderColor:"tan",
 
			},
			"&:nth-of-type(2n):before":
			{
				right:"auto",
				left:"-0.6rem",
				borderColor:"transparent transparent tomato tomato",
			}

		}

	},

		year:
		{
			textAlign:"center",
			maxWidth:"13rem",
			fontSize:'1.6rem',
			background:"tomato",
			lineHeight:1,
			color:"white",
			padding:"0.5rem 0 0.5rem",

			"&:before":
			{
				display:"none",
			},
			[theme.breakpoints.up("md")]:
			{
				textAlign:"center",
				margin:"0 auto",
				"&:nth-of-type(2n)":
				{
					float:"none",
					margin:"0 auto",
				},
				"&:nth-of-type(2n):before":
				{
					display:"none",
				}
			}
		},
		heading:
		{
			color:"tomato",
			padding:"3rem 0",
			textTransform:"uppercase",
			textAlign:"center"


		}
		,
		subheading:
		{
			color:"white",
			padding:"0",
			textTransform:"uppercase",

		},

		bodystyling:
		{
			color:"white",
			
            fontSize:"1.3rem",
			[theme.breakpoints.down("sm")]:{
			padding:"0",
			"&:before":
			{

				float:"left",
				
			}
		}
	}
}))







