import React from 'react';
import {makeStyles} from '@material-ui/styles';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';


const useStyles = makeStyles({
links:
{
	color:"#511",
	"&:hover":
	{   
		background:"tan"
	},
	marginTop:"5px",
	minWidth:"0",
	maxWidth:"250"
},
root:
{
"& .MuiBottomNavigationAction-root":
{
	minWidth:"0",
	maxWidth:"250",
},
"& .MuisvgIcon-root":
{
	fill: "tan",
	"&:hover":
	{   fill:"tomato",
		fontSize:"1.8rem",
	},
},
},
});



const Footer = () => {
	const classes = useStyles();
	return (
	
	<div className={classes.root} width="auto" style={{background:"tan",display:"flex",justifyContent:"space-around"}}>
    <a href="https://www.facebook.com/jermin.youssef" className={classes.links}><FacebookIcon/></a>
    <a href="https://www.linkedin.com/in/jermin-mansour-544296119/"  className={classes.links}><LinkedInIcon/></a>
    <a href="https://github.com/jermin12"  className={classes.links}><GitHubIcon/></a>
    </div>
	
	)
}

export default Footer