import React from 'react';
import NavBar from './NavBar';
import {makeStyles} from '@material-ui/core/styles';
import {Avatar,List,ListItem,ListItemIcon, ListItemText,Link} from '@material-ui/core';
import SmartphoneIcon from '@material-ui/icons/Smartphone';
import EmailIcon from '@material-ui/icons/Email';
import avatar from './curly2.jpg';
//import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import {Box,Card,CardContent,Grid,Typography,Divider} from '@material-ui/core';
import Particles from 'react-particles-js';



const useStyles = makeStyles(theme=>({
  container:
  {
    width:"100%",
    height:"100%",

  }
,
  root: 
  {
   opacity: 0.8,
    marginTop:'2%',
    width:"80%",
    height:"80%",
    maxWidth: 600,
   
    position:"absolute",
    top:"50%",
    left:"50%",
    transform:"translate(-50%,-50%)",
    background:"#344",
    color: "tomato",
    borderRadius:"16px",
    boxShadow: "5px 10px 18px maroon",
    paddingBottom:"1rem",


    [theme.breakpoints.down("sm")]:
    {
      minwidth:300,
      float:"left",
      margin:"5% auto"
     },
    [theme.breakpoints.down("md")]:
    {
      minwidth:450,
      float:"left",
      margin:"5% auto"
     }
  }

 ,
  avatar:
  {
    display:'block',
    margin:"7% auto",
    width: 150,
    height: 150,

     [theme.breakpoints.down("sm")]:
    {
     display:'block',
    margin:"10% auto",
  
    width: 100,
    height: 100,
      },
       [theme.breakpoints.down("md")]:
    {
       
    width:150,
    height: 150,

    }
  }
 
,

grid:
{
  margin:"0 auto",
  background:"maroon",
  width:"50%",
 // minWidth:"40%",
maxHeight:"100%",
 borderRadius:"16px",
[theme.breakpoints.down("sm")]:
  {
     width:"40%",
     background:"#344",
         
  }

 
}
,

title:
{
  marginTop:"1px",
  color:"white",
  textAlign:"center",
  [theme.breakpoints.down("md")]:
    {
  marginTop:"1px",
  color:"white",
  textAlign:"center",
  fontSize:"xxx-large",
    }
}
,

listcon:
{
  margin:"0px 0px",
  padding:"10px 0px",
  cursor:"text",
  textAlign:'left',
  fontSize:'1.1em',
  fontStretch:"extra-condensed",
 
  [theme.breakpoints.down("sm")]:
    {
  
    margin:"0.6rem",
    background:"maroon",
    borderRadius:"16px 16px",
    width:"15rem",
    position:"absolute",
    padding: "0",
    fontStretch:"ultra-condensed",
    fontSize:'0.7em',
     
    },
      
  [theme.breakpoints.down("md")]:
    {
  
    margin: "0.5rem",
    background:"maroon",
    borderRadius:"16px 16px",
    width:"16rem",
    position:"absolute",
    padding: "0",
    fontStretch:"ultra-condensed",
    fontSize:'1em',

    },
   

}
,

ListItem:
  {

   color: "tan",
    marginRight: "10px",
    marginLeft: "1px" ,
    whiteSpace:'pre',
    
  }

,

link:
{
  
  textDecoration:"none",
  
  margin:"4px 40px",
  color:"tan",
  fontSize:"1rem",
  '&onhover':
  {
      textDecoration:"none",
      cursor:"pointer",
      color:'tomato',
  }

    
}
}));





const Contacts = () => {

const classes = useStyles();
 return (
  <Box component="div" className={classes.container}>
   <NavBar/>
   

<Particles 
       params={{
      "particles":
     {
       "number": 
       {
       "value": 200,
       "density": 
            {"enable": true}
        },

       "size":
        {
          "value": 3,
          "random": true,
          "anim": 
          {
            "speed": 4,
            "size_min": 0.3
           }
        },

        "line_linked": 
        {"enable": false},
        "move":
         {
              "random": true,
              "speed": 1,
              "direction": "none",
              "out_mode": "bounce"
          }
      },

      "interactivity":

      {
         "events": {
              "onhover": 
              {
                  "enable": true,
                  "mode": "bubble"
              },
              "onclick": 
              {
                  "enable": true,
                  "mode": "repulse"
              }
          },
          "modes":
           {
              "bubble":
               {
                  "distance":400,
                  "duration": 2,
                  "size":0 ,
                  "opacity": 1
                },
              "repulse": 
              {
                  "distance": 400,
                  "duration": 4
              }
          }
      }
  }} />
 <Card className={classes.root}>
 <CardContent>
        <Typography variant="h3"  className={classes.title}>
           Let's Talk
        </Typography>
        <Divider/>
        <Avatar className={classes.avatar} alt="jermin" src={avatar} />
 <Grid container justify="center" className={classes.grid}>
    
  <List className={classes.listcon}>
    <ListItem>
    <ListItemIcon>
    <SmartphoneIcon/>
    </ListItemIcon>
    <ListItemText   className={classes.ListItem} primary="0466021004"/>
    </ListItem>

    <ListItem>    
    <ListItemIcon>
    <EmailIcon/>
    </ListItemIcon>
    <p  className={classes.ListItem}>Jermin.kamel@gmail.com</p>
    </ListItem> 

    <ListItem> 
    <br/>   
    <ListItemIcon>

    <GitHubIcon/>
    <Link className={classes.link} href="https://github.com/jermin12">
          Github Profile
     </Link>
    
   </ListItemIcon>
   
   
     
    </ListItem>
  </List>



 </Grid>

</CardContent>

</Card>

</Box>
  );
    
}


export default Contacts

	



