import React from 'react';
import NavBar from './NavBar';
import {makeStyles} from '@material-ui/core/styles';
import robologo from './robolo.png';
import tictactoe from './tictactoe.jpg';
import facerec from './facerec.png';
import WeatherApp from './WeatherApp.png';

import {Box,Grid,Card,CardActionArea,CardActions,CardContent,CardMedia,Typography,Button}
 from '@material-ui/core';


const useStyles = makeStyles({
  h1:
  {
    fontSize:'2.5rem',
    color:'black',
    textAlign:'center'
  }
  ,
    container:
    {
    Background:"tomato",
    height:"100%",

    }
     ,
     cardcontainer:
     {
     	maxWidth:340,
     	
     	margin: "3rem auto",
      overflow: 'auto',

      
     }
     ,
     cursor:
     {
      cursor:"text",
     }

}

)

const Portfolio = () => {

	const classes = useStyles();

	return (
		<Box component="div" className={classes.container}>
		 <NavBar/>
   <h1 className={classes.h1}>PROJECTS</h1>
   <Grid container  justify="center">

    <Grid item xs={12} sm={8} md={6}>
        <Card className={classes.cardcontainer}>
         <CardActionArea>
        {/* Project 1*/}
        <CardMedia
          component="img"
          alt="PR1"
          height="120"
          image={facerec}
          title="Celebrity Face Recognition"
        />
        <CardContent className={classes.cursor}>
        <Typography gutterbottom variant="h5">
        Project 1
        </Typography>
        <br/>
         <Typography variant="body2" color="textSecondary" component="p">
          <b>Face Recognition</b> is an App that recognises celebrities and detect their faces in an image address given by the user.
           <br/>
           Languages and Libraries used:
           HTML, 
           CSS,
           REACT JS,
           CLARIFAI API,
           TACHYONS.
          </Typography>
        </CardContent>
          <CardActions>
          <Button size="small" color="primary" href="https://jermin12.github.io/CelebrityFaceDetection/">
          LIVE DEMO
          </Button>
          </CardActions>
           </CardActionArea>
         </Card>
         </Grid>

    
         <Grid item xs={12} sm={8} md={6}>
        <Card className={classes.cardcontainer}>
         <CardActionArea>
        {/* Project 2*/}
        <CardMedia
          component="img"
          alt="PR1"
          height="120"
          image={robologo}
          title="Robo Friends"
        />
        <CardContent className={classes.cursor}>
        <Typography gutterbottom variant="h5">
       Project 2
        </Typography>
        <br/>
         <Typography variant="body2" color="textSecondary" component="p">
           <b>RoboFriends</b> is a robots searching app that looks up what the user types in and filter all 
           the Robots that have matching names and shows them.
           <br/>
           Languages and Libraries used:
           HTML,
           CSS,
           REACT HOOKS,
           TACHYONS.
           </Typography>
        </CardContent>
          <CardActions>
          <Button size="small" color="primary" href="https://jermin12.github.io/Robofriends/">
            Live Demo
          </Button>
         </CardActions>
           </CardActionArea>
         </Card>
          </Grid>







         <Grid item xs={12} sm={8} md={6}>
        <Card className={classes.cardcontainer}>
         <CardActionArea>
        {/* Project 3*/}
   		  <CardMedia
          component="img"
          alt="PR1"
          height="120"
          image={tictactoe}
          title="tic tac toe"
        />
        <CardContent className={classes.cursor}>
        <Typography gutterbottom variant="h5">
        Project 3
        </Typography>
        <br/>
         <Typography variant="body2" color="textSecondary" component="p">
           <b>Tic Tac Toe </b> game app gives each player a turn and shows the winner.
           <br/>
           Languages and Libraries used:
           HTML,
           CSS,
           REACT JS,
           TACHYONS.
          </Typography>
        </CardContent>
          <CardActions>
         	<Button size="small" color="primary" href="https://jermin12.github.io/tictactoe2/">
         		Live Demo
         	</Button>
         	</CardActions>
           </CardActionArea>
         </Card>
         </Grid>

        <Grid item xs={12} sm={8} md={6}>
        <Card className={classes.cardcontainer}>
         <CardActionArea>
        {/* Project 4*/}
   		  <CardMedia
          component="img"
          alt="PR1"
          height="120"
          image={WeatherApp}
          title="Weather App"
        />
        <CardContent className={classes.cursor}>
        <Typography gutterbottom variant="h5">
         Project 4
        </Typography>
        <br/>
         <Typography variant="body2" color="textSecondary" component="p">
           <b>Weather Forecast</b> is an app that lets the user types in any city name and
           shows its weather forecast.
           <br/>
           Languages and Libraries used:
           HTML,
           CSS,
           JAVASCRIPT,
           OPEN WEATHER MAP API.
          </Typography>
        </CardContent>
          <CardActions>
         	<Button size="small" color="primary" href="https://github.com/jermin12/weatherapp">
         		Show Code
         	</Button>
          </CardActions>
           </CardActionArea>
         </Card>
         </Grid>


        
 
      

       
		</Grid>
		</Box>
	)
}

export default Portfolio;