import React, {useState} from 'react'
import { makeStyles } from '@material-ui/core/styles';
import {Link} from 'react-router-dom';
import MobilRightMenuSlider from '@material-ui/core/Drawer';
import ViewHeadlineIcon from '@material-ui/icons/ViewHeadline';
import Footer from './Footer';

import {
 AppBar,
 Toolbar,
 ListItem,
 IconButton,
 ListItemText,
 ListItemIcon,
 Avatar,
 Divider,
 List,
 Typography,
 Box	
} from '@material-ui/core';

import {
AssignmentInd,
Home,
Apps,
ContactMail} from '@material-ui/icons';

import avatar from './curly2.jpg';

const useStyles = makeStyles(theme=>({
 menu: {
 	width:250,
    background: '#511',
    height: "100%",
  },
  avatar:
  {
  	display: "block",
  	margin: "0.5rem auto",
  	width: theme.spacing(15),
  	height: theme.spacing(15)

  },
  ListItem:
  {
  	color: 'tan'
  }
}));

const menuItems = [
{
	ListIcon: <Home/>,
	ListText:"Home",
  Listpath:'/'
},
{
	ListIcon: <AssignmentInd/>,
	ListText:"Resume",
  Listpath:'/resume'
}
,
{
	ListIcon: <Apps/>,
	ListText:"Projects",
   Listpath:'/portfolio'
},
{
	ListIcon: <ContactMail/>,
	ListText:"Contact",
  Listpath:'/contact'
}
]

const NavBar = () => {
     const [state, setState] = useState({right: false})

     const toggleslider = (slider,open) => () =>
     {
     	setState({...state, [slider]:open});
     }
	const classes = useStyles();
    const sidemenu = slider =>
      (
   
  <Box  className={classes.menu} component="div" onClick={toggleslider(slider,false)}>
  <Avatar className={classes.avatar} alt="jermin" src={avatar} />
  <Divider/>
  <List >
  {
  	menuItems.map((item,key)=>
  	{ return (	
  	<ListItem button key={key} component={Link} to={item.Listpath}>

  	<ListItemIcon className={classes.ListItem}>
  	{item.ListIcon}
  	</ListItemIcon>
   <ListItemText className={classes.ListItem} primary={item.ListText}/>
  	</ListItem>
  	);})


  }
  	
 </List>
</Box>

)

	return (
    <React.Fragment>
     <Box component="nav">
			<AppBar position="static" style={{background: "#ADD8E6"}}>
			       <Toolbar>
			             <IconButton onClick={toggleslider("right",true) }>
			                 <ViewHeadlineIcon style={{color:"#FFFFFF"}}/>
                         </IconButton>      
                          <Typography variant="h5" style={{color:"tan"}}>
                           Portfolio

                          </Typography>
                          <MobilRightMenuSlider open={state.right} onClose={toggleslider("right",false)}>
                          	{ sidemenu("right")}
                            <Footer/>
                          </MobilRightMenuSlider>

			        </Toolbar>
			</AppBar>
		</Box>
  

		

	</React.Fragment>
	)
}

export default NavBar;