import React from 'react';
import NavBar from './NavBar';
import Header from './Header';
import Particles from 'react-particles-js';
import {makeStyles} from '@material-ui/core/styles';


const useStyles = makeStyles({
	Partstyle:
	{
	position: "absolute",
	opacity: "0.4"
	}
}

)


const Home = () => {

	const classes = useStyles()

	return (
		<div>
		<NavBar/>
         <Header/>
         <Particles canvasClassName={classes.Partstyle}
          params={{
		 	"particles":
		 	{
                 "number": {
                        "value":50,
                        "density":
                        {
                        "value_area": 800,
                         "enable": true,
                        }
  
                    },
                    "shape":
                     { 
                    	"type":"star",

                    	"stroke": 
                    	{
                    		"width": 2,
                    	
                    	}
                    },
                    "size":
                    {
                    	"value": 3,
                    	"random": true,
                    	"anim":
                    	{
                    		"enable": true,
                    		"speed": 6,
                    		"size_min": 0.1,
                    		"sync": true
                    	}
                    },
                    "opacity":
                    {
                    	"value": 1,
                    	"random":true,
                    	"anim":
                    	{
                    		"enable":true,
                    		"speed": 1,
                    		"opacity_min":0.1,
                    		"sync": true
                    	}

                    }


                }

                }} />
		</div>
	)
}

export default Home;